<template>
<div class="table-page">
    <el-table v-loading="loading" :border="true" :data="data" row-class-name="table-column-back" :show-summary="true">
        <el-table-column v-for="(item,index) in datatitle" :key="index" :prop="item.prop" :label="item.label" :width="item.width" :min-width="item.minwidth" :fixed="item.fixed">
            <template slot-scope="scope">
                <render-custom-component v-if="item.component && item.component.name" :props="item.component.props" :component-name="item.component.name" :sync="item.comonent.sync" :value="scope.row[item.prop]">
                </render-custom-component>
                <render-component v-else-if="item.component && item.component.render" :render-function="item.component.render" :scope="scope"></render-component>
                <template v-else>
                    {{item.formatter?item.formatter(scope.row,scope.column,scope.row[item.prop],scope.$index):tableDataFormatter(scope.row,scope.column,scope.row[item.prop],scope.$index)}}
                </template>
            </template>
        </el-table-column>
        <el-table-column :label="opreate.label" fixed="right" :min-width="opreate.minwidth">
            <template slot-scope="scope">
                <el-tooltip class="item" effect="light" :open-delay="100" placement="bottom-end" :content="item.text" :disabled="!item.circle" v-for="(item,index) in opreate.btn" :key="index">
                    <el-button :icon="item.icon" :circle="item.circle" :round="item.round" :type="item.type" :size="item.size" @click="item.btnfun(scope.row)" v-show="item.show">
                        <span v-if="!item.circle">{{item.text}}</span>
                    </el-button>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
    <div class="aibd-page">
        <el-pagination  @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="active" :page-sizes="pagesizes" :page-size="pagesize" :layout="layout" :total="listTotal">
        </el-pagination>
    </div>
    
</div>
</template>

<script>
export default {
    components: {
        renderComponent: () => import('./renderComponent'),
        renderCustomComponent: () => import('./renderCustomComponent'),
    },
    props: {
        url: {
            type: String,
            default: "",
            required: true
        },
        parems: {
            type: Object,
            default: () => {
                return {}
            }
        },
        datatitle: {
            type: Array,
            default: () => {
                return [] //内部可选参数
                // {
                //     prop: String, 对应列内容的字段名
                //     minwidth: String,  对应列的最小宽度，与 width 的区别是 width 是固定的，min-width 会把剩余宽度按比例分配给设置了 min-width 的列
                //     label: String, 对应列的标题
                //     width: String, 对应列的宽度
                //     fixed: Boolean|String, 是否固定列，可选 true，right，left
                //     formatter: Function, 格式化内容	Function(row, column, cellValue, index)
                //     component: { 注入自定义组件
                //         name: String, 可选，渲染自定义组件名
                //         props: Object, name存在时注入组件使用注入属性
                //         sync: Boolean, 是否双向绑定数据
                //         render:Function, jsx渲染自定义组件 Function(h,row,column,index)
                //     }
                // }
            }
        },
        opreate: {
            required: true,
            type: Object,
            default: () => {
                return {
                    label: "操作",
                    minwidth: "100",
                    btn: [
                        // {
                        //     type: String, 按钮类型，类型参照element-ui按钮组件type ,可选primary / success / warning / danger / info / text
                        //     size: String, 按钮大小，类型参照element-ui按钮组件size ，medium / small / mini
                        //     text: String, 按钮显示内容，若circle为true时，为tooltip显示内容
                        //     icon: String, 按钮绑定图标类名，可自行引入icon css，按照css引入形式引入
                        //     circle:Boolean, 是否为圆型按钮
                        //     btnfun: Function, 按钮绑定事件，Function(row)
                        //     show: Boolean, 是否显示 
                        //     round:Boolean, 是否为圆角按钮
                        //     
                        // }
                    ], // 按钮操作绑定信息

                }
            }
        },
        initrequest: { //表格初始化数据请求
            type: Boolean,
            default: false
        },
        pagshow: { //分页是否显示
            type: Boolean,
            default: true
        }
    },
    watch: {
        $route(news) {
            this.$store.commit("re_init_page");
            console.log(this.opreate)
        },
        'parems'(news) {
            this.logindata();
        }
    },
    mounted() {
        this.logindata();
    },
    data() {
        return {
            layout: "total, sizes, prev, pager, next, jumper", //分页子组件顺序 默认 'prev, pager, next, jumper, ->, total'
            loading: false
        }
    },
    computed: {
        active() {
            return this.$store.getters.get_active;
        },
        data() {
            return this.$store.getters.get_data;
        },
        pagesizes() {
            return this.$store.getters.get_pagesizes;
        },
        pagesize() {
            return this.$store.getters.get_pagesize;
        },
        listTotal() {
            return this.$store.getters.get_listTotal;
        },
        count() {
            return this.$store.getters.get_count;
        },
    },
    methods: {
        async logindata() {
            this.loading = true;
            await this.$store.dispatch("get_table_data", {
                url: this.url,
                parems: this.parems
            });
            this.loading = false;
        },
        handleSizeChange(pagesize) {
            this.$store.commit("update_pagesize", pagesize);
            this.logindata();
        },
        handleCurrentChange(active) {
            this.$store.commit("update_active", active);
            this.logindata();
        },
        tableDataFormatter(row, column, cellValue, index) {
            if (cellValue == undefined || cellValue == "") {
                return "空"
            } else {
                return cellValue;
            }
        }
    }
}
</script>

<style scoped>
.aibd-page {
    width: 100%;
    border-top: 1px solid #ebeef5;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 5px 15px 15px 20px;
    box-sizing: border-box;
    background-color: #fff9;
}

/* .container_table_back {
    background-color: rgba(221, 221, 221, 0.774) !important;
}

.container_table_back .table-column-back {
    background-color: rgba(255, 255, 255, 0.253) !important
} */
</style>
